body {
  --main-color: #060b35;
  --secondary-color: #616070;
  --button-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product__image {
  width: 160px;
  height: 134px;
  margin: 0 0 40px;
  display: block;
}

.product__name {
  color: var(--main-color);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: var(--secondary-color);
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product__reviewsQuantity {
  color: var(--main-color);
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.product__priceWord {
  color: var(--secondary-color);
  font-size: 12px;
  line-height: 18px;
}

.product__priceCost {
  color: var(--main-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__botton {
  box-sizing: border-box;
  border: 1px solid var(--button-color);
  background-color: var(--button-color);
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product__botton:hover {
  color: var(--button-color);
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 span:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
